import Profile from "../../info.json";
import "./skills.css"

const Skills = () => {
    return (
        <div className="row">
            <h3>Skills</h3>
            <ul className="skil-list">
                {
                    Profile.skills.map((skill, index) => <li key={index} className="skil-list-item">{skill}</li>)
                }
            </ul>

        </div>
    )
}

export default Skills;